.hero {
    background-color: $grey-color;
    position: relative;
    &\--index {
        background-image: url('../img/hero-mobile.png');
        background-repeat: no-repeat;
        background-position: 50% -14px;
        background-size: auto $hero-height-mobile--index * 1.3;
        background-color: $grey-color--index;
    }
    &__top-links {
        height: 40px;
    }
    &__title {
        font-size: 27px;
        line-height: 30px;
        font-weight: 600;
        text-align: center;
        position: absolute;
        width: 90%;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
    }
    &__title\--index {
        top: $hero-height-mobile--index * 0.25;
        transform: translateX(-50%);
    }
    @media(min-width: $tablet) {
        &::after {
            content: '';
            display: block;
            position: absolute;
            height: 24px;
            width: 100%;
            top: 100%;
            left: 0;
            background: url('../img/wave.svg') repeat-x;
            background-size: auto 100%;
            background-position: bottom -4px left -8px;
            background-color: $grey-color;
        }
        &\--index {
            background-image: url('../img/hero-desktop-tablet.png');
            background-position: 50% 4px;
            background-size: auto $hero-height-tablet-desktop--index * 1.18;
            &::after {
                display: none;
            }
        }
        &__title {
            font-size: 43px;
            line-height: 50px;
            top: 55%;
        }
        &__title\--index {
            top: $hero-height-tablet-desktop--index * 0.24;
        }
    }
    @media(min-width: $desktop) {
        border-top: 1px solid $grey-border-color;

        &__title {
            top: 50%;
            font-size: 53px;
            line-height: 57px;
        }
        &::after {
            height: 34px;
        }
    }
}