@font-face {
    font-family: "Open Sans";
    src: url("../fonts/openSans/OpenSans-Regular.ttf") format("opentype");
}
@font-face {
    font-family: "Open Sans";
    src: url("../fonts/openSans/OpenSans-Bold.ttf") format("opentype");
    font-weight: bold;
}

*,
*::after,
*::before {
    box-sizing: border-box;
}

body {
    margin: 0;
    padding: 0;
    font-family: "Open Sans", sans-serif;
    font-size: 17px;
}

$top-menu-height-mobile: 80px;
$top-menu-height-tablet-desktop: 90px;
$tablet: 600px;
$desktop: 900px;
$hero-height-mobile--index: 282px;
$hero-height-mobile: 175px;
$hero-height-tablet-desktop--index: 625px;
$hero-height-tablet: 213px;
$hero-height-desktop: 235px;
$page__item-width-tablet: 87%;
$grey-color--index: #f9f9f9;
$grey-color: #f1f1f1;
$dark-grey-color: #c7c7c7;
$grey-border-color: #ececec;
$green-color: #63d1bb;
$green-color-2: #45c1ae;
$small-wave-height-mobile: 7px;
$small-wave-height-tablet: 17px;
$small-wave-height-desktop: 23px;
$hover-opacity: 0.5;
$down-opacity: 0.28;